import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Adapt(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/adapt.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[2.93, -1.21, -7.11]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere157.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere157_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere157_2.geometry}
          material={materials.outline}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere157_3.geometry}
          material={materials["glass.005"]}
        />
      </group>
    </group>
  );
}
export default Adapt;

useGLTF.preload("/glossarySelected/adapt.glb");
